.app__gallery{
    display: flex;
    flex-direction: row;
    padding: 4rem 0 4rem 6rem;
}

.app__gallery-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding-right: 2rem;
}

.app__gallery-images{
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;


}
.app__gallery-images_container{
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    scrollbar-width: none;
}
.app__gallery-images_container::-webkit-scrollbar{
    display: none;
}

.app__gallery-images_card{
    position: relative;
    min-width: 300px;
    height: 470px;
    margin-right: 2rem;
}

.gallery__image-icon {
    position: absolute;
    color: var(--color-golden);
    font-size: 2rem;
    opacity: 0;
    transition: .5s ease;
    cursor: pointer;
}
.app__gallery-images_card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: .5s ease;
    
}
.app__gallery-images_card:hover img{
    opacity: 0.45;
}
.app__gallery-images_card:hover .gallery__image-icon{
    opacity: 1;
}

.app__gallery-images_arrows{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 5%;
    padding: 0 1rem;
}
.gallery__arrows-icon{
    color: var(--color-black);
    font-size: 2rem;
    cursor: pointer;
    background-color: var(--color-golden);
    border-radius: 5px;

}
.gallery__arrows-icon:hover {
    color: var(--color-golden);
    transition: .7s ease;
    background-color: var(--color-black);
}
@media screen and (min-width: 2000px) {
    .app__gallery-content button {
        margin-top: 2rem;
    }

    .app__gallery-content {
        min-width: 1000px;
        padding-right: 4rem;
    }

    .app__gallery-images_card {
        min-width: 400px;
        height: 547px;
    }
}

@media screen and (max-width: 1150px) {
    .app__gallery {
        flex-direction: column;
    }

    .app__gallery-images {
        max-width: 100%;
        margin: 5rem 0;
    }
}

@media screen and (max-width: 850px) {
    .app__gallery {
        padding: 4rem 3rem 4rem 4rem;
    }
}

@media screen and (max-width: 650px) {
    .app__gallery {
        padding: 4rem 1rem 4rem 2rem;
    }  
    
    .app__gallery-content {
        min-width: 100%;
    }

    .app__gallery-images_card {
        min-width: 220px;
        height: 290px;
    }
}